    background-color: rgb(167, 195, 242);
    border-radius: 50%;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    -webkit-transform: scale(0.001, 0.001);
            transform: scale(0.001, 0.001);
}
.Button_button__WWyIb:focus:before {
    -webkit-animation: Button_effect_dylan__1CcDs 0.8s ease-out;
            animation: Button_effect_dylan__1CcDs 0.8s ease-out;
    
}
@-webkit-keyframes Button_effect_dylan__1CcDs {
  50% {
    -webkit-transform: scale(1.5, 1.5);
            transform: scale(1.5, 1.5);
    opacity: 0;
  }
  99% {
    -webkit-transform: scale(0.001, 0.001);
            transform: scale(0.001, 0.001);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(0.001, 0.001);
            transform: scale(0.001, 0.001);
    opacity: 1;
  }
}
@keyframes Button_effect_dylan__1CcDs {
  50% {
    -webkit-transform: scale(1.5, 1.5);
            transform: scale(1.5, 1.5);
    opacity: 0;
  }
  99% {
